Button{

--background-color:transparent;
  background-image: linear-gradient(to bottom right,rgba(0,0,0,0.3),(0,0,0,0));
    padding:5px;
    
      align-items: center;
  justify-content: center;
  --box-shadow: 1px 1px white inset;
  --box-shadow: 1px 1px -1em -1em white inset;
  --background-color: #bacad9;
  filter: blur(0.1px);
  border-radius:20px 20px 20px 20px;
  background-image: linear-gradient(to bottom right,rgba(255,255,255,0.3),rgba(255,255,255,0));
  border: 0.1px solid white;

}

.Container{

  align-items: center;
  justify-content: center;
  --box-shadow: 1px 1px white inset;
  --box-shadow: 1px 1px -1em -1em white inset;
  --background-color: #bacad9;
  filter: blur(0.1px);
  border-radius:20px 20px 20px 20px;
  --background-image: linear-gradient(to bottom right,rgba(255,255,255,0.3),rgba(255,255,255,0));
  border: 0.1px solid white;
}

.neo_back{
  align-items: center;
  justify-content: center;
  box-shadow:  10px 10px 50px #000000;
  --box-shadow:  10px 10px 10px #000000 outset;
  --box-shadow: 10px 10px black;
  --background-color: #bacad9;
  filter: blur(0.1px);
  border-radius:20px 20px 20px 20px;
  background-image: linear-gradient(to bottom,rgba(255,255,255,0),rgba(255,255,255,0));
  --border: 1px solid black;
	min-height: 20px;
	--padding:20px;
	overflow: hidden;
	min-width:480px;
	--min-height:1080px;
			transform: translatex(0px);
	animation: floatz 6s ease-in-out infinite;
}

.neo_glass{
  align-items: center;
  justify-content: center;
  box-shadow:  10px 10px 50px grey;
  --box-shadow:  10px 10px 10px #000000 outset;
  --box-shadow: 10px 10px black;
  --background-color: #bacad9;
  filter: blur(0.1px);
  border-radius:60px 60px 180px 20px;
  background-image: linear-gradient(to bottom,rgba(240,240,240,0),rgba(255,255,255,0));
  --border: 1px solid black;
	min-height: 20px;
	--padding:20px;
		transform: translatez(0px);
	animation: floatz 2s ease-in-out infinite;

		transform: translatex(0px);
	animation: floatx 10s ease-in-out infinite;



}

.neo_glass2{
  align-items: center;
  justify-content: center;
  box-shadow:  1px 1px 10px grey;
  --box-shadow:  10px 10px 10px #000000 outset;
  --box-shadow: 10px 10px black;
  --background-color: #bacad9;
  filter: blur(0.1px);
  border-radius:200px 360px 30px 120px;
  background-image: linear-gradient(to bottom,rgba(255,255,255,1),rgba(255,255,255,0));
  --border: 1px solid black;
	--min-height: 20px;
	padding:10px;

		transform: translatex(0px);
	animation: floatx 6s ease-in-out infinite;


}
.neo_padding{
padding:20px;
}




.float_animex{
		transform: translatex(0px);
	animation: floatx 10s ease-in-out infinite;
}



@keyframes floaty {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(0px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatey(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(0px);
	}
}


@keyframes floatx {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatex(20px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatex(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatex(20px);
	}
}

@keyframes floatz {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatez(0px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatez(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatez(0px);
	}
}


